/* eslint-disable react/prop-types */
import React, { Component } from 'react';
import { RichContentEditor, EditorState } from 'wix-rich-content-editor';
import { ContentState, convertFromHTML } from 'wix-rich-content-editor-common';
import 'wix-rich-content-editor-common/dist/styles.min.css';
import 'wix-rich-content-editor/dist/styles.min.css';

class Editor extends Component {
  constructor(props, context) {
    super(props, context);
    const blocksFromHTML = convertFromHTML(this.props.value || ' ');
    const contentState = ContentState.createFromBlockArray(
      blocksFromHTML.contentBlocks,
      blocksFromHTML.entityMap,
    );
    this.state = {
      editorState: EditorState.createWithContent(contentState),
    };
  }

  onChange = (value) => {
    this.setState({ editorState: value });
    console.log(value);
  };

  render() {
    const {
      forID,
      classNameWrapper,
      setActiveStyle,
      setInactiveStyle,
    } = this.props;
    return (
      <div className="cms-editor-visual" htmlFor={forID}>
        <div className={classNameWrapper}>
          <RichContentEditor
            className={classNameWrapper}
            onChange={this.onChange}
            editorState={this.state.editorState}
            onFocus={setActiveStyle}
            onBlur={setInactiveStyle}
          />
        </div>
      </div>
    );
  }
}

export default Editor;
